.display-onsiteupload{
  max-width: 100%;
  width: 100%;
  z-index: 9998;
  position: absolute;
  left: 0;
  right: 0;
  padding: 15px;
  border-radius: 0px;
  text-align: center;
  display: flex;
  align-items: center;
  justify-content: center;
  color: #fff;
  background-color: #000;
}
.onsiteupload_top_position{
top:0px;
}
.onsiteupload_bottom_position{
 bottom: 0px;
}

.onsite_barcode_img_{
  height: 100%;
  width: auto;
  max-width: 100%;
}

.onsite_barcode_{
  display: flex;
  flex-shrink: 1;
  align-items: center;
  height: 100%;
  min-width: 70px;
}

.onsite_text_{
  padding: 0 15px;
  display: flex;
  align-items: center;
  justify-content: start;
  text-align: left;
}