/** Hotspot CSS Start **/
.blob_001 {
    border-radius: 50%;
    margin: 0;
    height: 16px;
    width: 16px;
    transform: scale(1);
    background: #ffffff;
    box-shadow: 0 0 0 0 rgba(0, 0, 0,1);
    animation: pulse-dark 2s infinite;
    display: inline-block;
}

@keyframes pulse-dark {
	0% {
		transform: scale(.85);
		box-shadow: 0 0 0 0 rgba(0, 0, 0, .7)
	}
	70% {
		transform: scale(1)
	}
	99% {
		box-shadow: 0 0 0 8px rgba(0, 0, 0, 0)
	}
	100% {
		transform: scale(.85);
		box-shadow: 0 0 0 0 rgba(0, 0, 0, 0)
	}
}

.hotspot-summary-section.hotspot_top_ {
