.ts_ct_post_container{
    width: 100%;
}

.ts_ct_post_wrapper2xleft {
    float: left;
    padding: 7.5px;
}

.ts_ct_post_wrapper2xright {
    float: right;
    padding: 7.5px;
}

.ts_cs_post_row {
    width: 100%;
}

.ts_ct_post_wrapper{
    width: 20%;
    padding: 7.5px;
    float: left;
}

.ts_ct_social_rating{
    height: 26px;
    width: 125px;
    background-repeat: no-repeat;
}

.ts_ct_button{
    color: rgb(255, 255, 255);
    border: 1px solid rgb(0, 0, 0); 
    padding: 5px;
    font-family: Inter;
     border-radius: 3px
}

.ts_ct_post_in {
    overflow: hidden;
    position: relative;
    cursor: pointer;
}

.ts_cs_post_large{
    width: 50%; 
    float: left;
}

.ts_cs_post_small{
    width: 50%; 
    float: left;
}

.ts_ct_image {
    position: relative;
    display: block;
    max-width: 100%;
    max-height: 100%;
    width: 100%;
    object-fit: cover;
    object-position: center;
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    background-repeat: no-repeat;
    background-size: cover;
}

.ts_ct_image_blur_bg {
    background-position: 50%;
    background-repeat: no-repeat;
    background-size: cover;
    height: 100%;
    left: 0;
    padding-bottom: 100%;
    right: 0;
    top: 0;
    width: 100%;
    transition: .2s linear;
    -moz-transition: .2s linear;
}

.ts_ct_post_in:hover .ts_ct_social_warpper{
    opacity: 1;
}

.ts_ct_social_warpper {
    position: absolute;
    width: 100%;
    height: 100%;
    left: 0;
    top: 0;
    right:0;
    bottom:0;
    opacity: 0;
    background-color: rgba(0,0,0,.8);
    transition: .2s linear;
    -moz-transition: .2s linear;
}

.ts_ct_social_wrappeer_in {
    position: absolute;
    left: 50%;
    top: 50%;
    z-index: 9999;
    transform: translate(-50%, -50%);
    -ms-transform: translate(-50%, -50%);
    -moz-transform: translate(-50%, -50%);
    -webkit-transform: translate(-50%, -50%);
}

.ts_ct_social_ {
    font-size: 24px;
    text-align: center;
    
}

.ts_ct_image_blur_bg {
    height: auto;
    width: 100%;
    padding-bottom: 100%;
    background-size: cover;
    background-repeat: no-repeat;
    background-position: 50%;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    vertical-align: top;
    filter: blur(30px);
    -webkit-filter: blur(10px);
    -moz-filter: blur(10px);
    -ms-filter: blur(10px);
    -o-filter: blur(10px);
}

.ts_ct_button {
    color: #fff;
    border: 1px solid #fff;
    border-radius: 0px;
    padding: 5px;
    font-weight: 400;
    font-style: normal;
    white-space: nowrap;
    text-overflow: ellipsis;
    overflow: hidden;
    font-size: 14px;
    margin-top: 15px;
}

.ts_ct_social_warpper.ts_black_bg_hover .ts_ct_button {
    background: rgba(255,255,255,0);
    color: #fff;
    border: 1px solid #fff;
    font-weight: 500;
}

.ts_ct_social_warpper.ts_white_bg_hover .ts_ct_button {
    background: rgba(0,0,0,1);
    color: #fff;
    border: 1px solid #000;
}

.ts_ct_social_warpper.ts_black_bg_hover .ts_ct_button:hover {
    background: rgba(255,255,255,1);
    color: #000;
    border: 1px solid #fff;
}

.ts_ct_post_wrapper2xleft .ts_ct_image, .ts_ct_post_wrapper2xright .ts_ct_image {
    height: 1015px !important;
}

.ts_st_post_hotspot{
    width: 100%;
    height: 100%;
    position: absolute;
    z-index: 4;
    top: 50%;
    transform: translateY(-50%);
    left: 0;
    right: 0;
    margin: 0 auto;
}

.ts_st_post_hotspot_icon{
    position: absolute;
    cursor: pointer;
    display: flex;
    left:50%;
    top:50px;
}

.ts_st_post_hotspot_icon_blob{
    border-radius: 50%;
    margin: 0;
    height: 16px;
    width: 16px;
    transform: scale(1);
    background: #ffffff;
    box-shadow: 0 0 0 0 rgba(0, 0, 0, 1);
    animation: pulse-dark 2s infinite;
    display: inline-block;
}
.ts_ct_image img{
    margin: 0px;
    padding: 0px;
}

@keyframes pulse-dark {
	0% {
		transform: scale(.85);
		box-shadow: 0 0 0 0 rgba(0, 0, 0, .7)
	}
	70% {
		transform: scale(1)
	}
	99% {
		box-shadow: 0 0 0 8px rgba(0, 0, 0, 0)
	}
	100% {
		transform: scale(.85);
		box-shadow: 0 0 0 0 rgba(0, 0, 0, 0)
	}
}

@media screen and (max-width:767px){
    .ts_ct_post_in:hover .ts_ct_social_warpper{
        display: none;
    }
}