.media__wrap__{
    position: absolute;
    width: 100%;
    top: 0;
    left: 0;
    right: 0;
}
.tb_sc_post_container{
    display: flex;
    flex-wrap: wrap;
    width: 100%;
}

.tb_sc_post_wrapper{
    width: 25%;
    padding: 7.5px;
}

.tb_sc_post_in{
    display: block;
    position: relative;
    box-shadow: 0 0 1.5px rgba(0, 0, 0, 0.20);
    cursor: pointer;
}

.tb_sc_image {
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
    position: relative;
}

.tb_sc_image img{
    width: 100%;
    position: absolute;
    left: 0;
    right: 0;
    top: 0;
    height: 100%;
    object-fit: cover;
    object-position: center;
    margin: 0px;
    padding: 0px;
}

.tb_sc_hash_tag{
    display: inline;
}

.tb_sc_contant_wrapper {
    padding: 10px;
}

.tb_sc_author_wrapper {
    margin-bottom: 10px;
}

.tb_sc_author {
    width: calc(100% - 30px);
}

.tb_sc_author_profile {
    height: 44px;
    width: 44px;
    border-radius: 50%;
    margin-right: 10px;
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
    display: flex;
    flex-shrink: 0;
    overflow: hidden;
}


.tb_sc_authorname {
    font-size: 14px;
    font-weight: 600;
    margin-bottom: 4px;
    color: #2b2b2b;
    white-space: nowrap;
    font-family: 'Inter';
    display: block;
    overflow: hidden;
    text-overflow: ellipsis;
}

.tb_sc_author {
    display: flex;
    align-items: center;
    flex-grow: 1;
}

.tb_sc_author_wrapper {
    display: flex;
    align-items: center;
    justify-content: end;
}

.tb_sc_author_info {
    overflow: hidden;
    display: flex;
    flex-shrink: 0;
    flex-direction: column;
    width: calc(100% - 54px);
    padding-right: 10px;
}

.tb_sc_post_info {
    display: flex;
    align-items: center;
}

.tb_sc_seprator {
    height: 4px;
    width: 4px;
    background-color: #2b2b2b;
    border-radius: 50%;
    margin: 0 6px;
    opacity: 0.8;
    display: flex;
    flex-shrink: 1;
}

.tb_sc_username, .tb_sc_time {
    font-size: 10px;
    color: #2b2b2b;
    font-weight: 500;
    opacity: 0.8;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
    font-family: 'Inter';
}

.tb_sc_username{
    max-width: 65%;
}

.tb_sc_time{
    max-width: 35%;
}

.tb_sc_social_ {
    font-size: 24px;
}

.tb_sc_content {
    font-size: 14px;
    text-overflow: ellipsis;
    overflow: hidden;
    word-break: break-word;
    padding: 10px 0;
}

.ts_cp_media_wrap {
    position: relative;
}

.ts_ct_button{
    width: 100%;
    text-align: center;
    border-radius: 0px;
    -moz-border-radius: 0px;
    -webkit-border-radius: 0px;
    transition: all .5s ease;
    padding: 9px 15px;
    display: block;
    text-transform: uppercase;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    margin-bottom: 10px;
    font-size: 14px;
}

.ts_sc_share-icon_wrapper {
    position: absolute;
    top: -15px;
    left: 15px;
    opacity: 0;
    visibility: hidden;
    -webkit-backface-visibility: hidden;
    transition: all .1s ease 0s;
    z-index: 1400;
}

.ts_sc_share-icon_wrapper a {
    display: flex;
    align-items:center;
    justify-content: center;
    background: #fff;
    cursor: pointer;
    font-size: 16px!important;
    width: 30px;
    height: 30px;
    line-height: 30px!important;
    text-align: center;
    position: relative;
    border-radius: 30px;
    -moz-border-radius: 30px;
    -webkit-border-radius: 30px;
    box-shadow: 0 1px 3px rgba(169, 167, 167, 0.5);
    -moz-box-shadow: 0 1px 3px rgba(169,167,167, 0.5);
    -webkit-box-shadow: 0 1px 3px rgba(169, 167, 167, 0.5);
    transition: all .2s ease;
    color: #333;
}

.ts_sc_share-icon_wrapper a {
    position: absolute;
    -webkit-transform: translateZ(0);
    transform: translateZ(0);
    transition: all .3s ease;
}

.ts_sc_share-icon_wrapper a:first-child {
    z-index: -1;
}

.ts_sc_share-icon_wrapper a:nth-child(2) {
    z-index: -2;
}

.ts_sc_share-icon_wrapper a:nth-child(3) {
    z-index: -3;
}

.ts_sc_share-icon_wrapper a:nth-child(4) {
    z-index: -4;
}

.tb_sc_post_in:hover .ts_sc_share-icon_wrapper {
    opacity: 1;
    visibility: visible;
    top: 10px;
}

.tb_sc_post_in:hover .ts_sc_share-icon_wrapper:hover a {
    opacity: 1;
    position: relative!important;
}

.tb_sc_post_in .ts_sc_share-icon_wrapper:hover a:nth-child(2) {
    -webkit-transform: translate3d(0,10px,0);
    transform: translate3d(0,10px,0);
    left: 0;
}

.tb_sc_post_in .ts_sc_share-icon_wrapper:hover a:nth-child(3) {
    -webkit-transform: translate3d(0,20px,0);
    transform: translate3d(0,20px,0);
    left: 0;
}

.tb_sc_post_in .ts_sc_share-icon_wrapper:hover a:nth-child(4) {
    -webkit-transform: translate3d(0,30px,0);
    transform: translate3d(0,30px,0);
    left: 0;
}

.tb_sc_media_wrap{
    position: relative;
}

.ts_st_post_hotspot{
    width: 100%;
    height: 100%;
    position: absolute;
    z-index: 4;
    top: 50%;
    transform: translateY(-50%);
    left: 0;
    right: 0;
    margin: 0 auto;
}

.ts_st_post_hotspot_icon{
    position: absolute;
    cursor: pointer;
    display: flex;
    left:50%;
    top:50px;
}

.ts_st_post_hotspot_icon_blob{
    border-radius: 50%;
    margin: 0;
    height: 16px;
    width: 16px;
    transform: scale(1);
    background: #ffffff;
    box-shadow: 0 0 0 0 rgba(0, 0, 0, 1);
    animation: pulse-dark 2s infinite;
    display: inline-block;
}


@keyframes pulse-dark {
	0% {
		transform: scale(.85);
		box-shadow: 0 0 0 0 rgba(0, 0, 0, .7)
	}
	70% {
		transform: scale(1)
	}
	99% {
		box-shadow: 0 0 0 8px rgba(0, 0, 0, 0)
	}
	100% {
		transform: scale(.85);
		box-shadow: 0 0 0 0 rgba(0, 0, 0, 0)
	}
}