.ts_csl_image {
    display: block;
    left: 50%;
    max-height: 100%;
    max-width: 100%;
    object-fit: cover;
    object-position: center;
    position: relative;
    position: absolute;
    top: 50%;
    -webkit-transform: translate(-50%,-50%);
    transform: translate(-50%,-50%);
    aspect-ratio: 1/1;
    
}
.ts_cst_inner_img_overflow {
    overflow: hidden;
    height: 100%;
}
.ts_cst_inner_img_overflow img{
    margin: 0px;
    padding: 0px;
    aspect-ratio: 1/1;
    object-fit: cover;
    vertical-align: bottom;
}
