.ts_csl_splide__arrow.splide__arrow{
    top:200px;
    width:35px;
    height:50px;
    background: #fff;
    color:#000;
    opacity: 0.8;
    font-size: 22px;
}
.hotsopt_image{
    top: 50%;
    transform: translate(0%, -50%);
    aspect-ratio:auto;
}

.splide__arrow--next.ts_csl_splide__arrow {
    right: 2px;
    border-radius: 3px 0 0 3px;
    box-shadow: -1px 0 3px rgba(0, 0, 0, 0.6);
}

.splide__arrow--prev.ts_csl_splide__arrow {
    left:0px;
    border-radius: 0 3px 3px 0;
    box-shadow: 1px 0 3px rgba(0, 0, 0, 0.3);
}

.ts_cs_author_wrapper {
    padding: 10px 0px;
}

.ts_cs_content_wrapper {
    padding: 10px 20px 20px;
    text-align: center;
    min-height: 220px;
}

.ts_cs_content {
    font-family: Inter;
    font-size: 14px;
    color: #707070;
    margin-top: 10px;
    word-break: break-word;
    line-height: 24px;
    display: -webkit-box;
    -webkit-line-clamp: 3;
    -webkit-box-orient: vertical;
    overflow: hidden;
}

.ts_cs_content a{
    background-color: transparent;
}

.ts_cs_author {
     text-align: center;
}

.ts_cls_author_profile {
    height: 44px;
    width: 44px;
    border-radius: 50%;
    margin: 0 auto;
    background-repeat: no-repeat;
    margin-bottom: 10px;
    position: relative;
    box-shadow: 0px 0px 1px;
    margin-top: -41px;
    overflow: hidden;
}

.ts_cls_author_profile img{
    height: 44px;
    width: 44px;
    border-radius: 50%;
    background-size: cover;
    background-position: center;
    overflow: hidden;
    border: 2px solid #fff;
}

.ts_cls_author_profile_img{
    width: 100%;
}
.ts_cs_authorname {
    font-size: 14px;
    font-weight: 600;
    margin-bottom: 4px;
    color: #2b2b2b;
    white-space: nowrap;
    display: block;
    overflow: hidden;
    text-overflow: ellipsis;
    font-family: "Inter";
}

.ts_cs_author_info {
    overflow: hidden;
    margin-bottom: 15px;
}

.ts_cs_post_info {
    display: flex;
    align-items: center;
    justify-content: center;
}

.ts_cs_seprator {
    height: 4px;
    width: 4px;
    background-color: #2b2b2b;
    border-radius: 50%;
    margin: 0 6px;
    opacity: 0.8;
    display: flex;
    flex-shrink: 1;
}

.ts_cs_username, .ts_cs_time {
    font-size: 12px;
    color: #2b2b2b;
    font-weight: 500;
    opacity: 0.8;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
    font-family: "Inter";
}

.ts_cs_username{
    max-width: 65%;
}

.ts_cs_time{
    max-width: 35%;
}

.ts_csl_post_slick-next .ts_cs_social_, .ts_csl_post_slick-prev .ts_cs_social_ {
    font-size: 35px;
    text-align: center;
}

.ts_csl_post_in .ts_cs_social_{
    font-size: 24px;
}

.ts_cs_btn_wrapper {
    padding: 0 20px;
}

.ts_cs_button{
    width: 100%;
    font-size: 14px;
    text-align: center;
    border-radius: 3px;
    -moz-border-radius: 3px;
    -webkit-border-radius: 3px;
    transition: all .5s ease;
    background: #333;
    color: #fff;
    padding: 8px 0 8px 8px;
    display: block;
    text-transform: uppercase;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    margin-top: 10px;
}

.tb_cls_media_wrap {
    position: relative;
    overflow: hidden;
}

.ts_st_post_hotspot{
    width: 100%;
    height: 100%;
    position: absolute;
    z-index: 4;
    top: 50%;
    transform: translateY(-50%);
    left: 0;
    right: 0;
    margin: 0 auto;
}

.ts_st_post_hotspot_icon{
    position: absolute;
    cursor: pointer;
    display: flex;
    left:50%;
    top:50px;
}

.ts_st_post_hotspot_icon_blob{
    border-radius: 50%;
    margin: 0;
    height: 16px;
    width: 16px;
    transform: scale(1);
    background: #ffffff;
    box-shadow: 0 0 0 0 rgba(0, 0, 0, 1);
    animation: pulse-dark 2s infinite;
    display: inline-block;
}

.ts_cs_image_blur_bg {
    background-position: 50%;
    background-repeat: no-repeat;
    background-size: cover;
    bottom: 0;
    filter: blur(30px);
    -webkit-filter: blur(10px);
    -moz-filter: blur(10px);
    -ms-filter: blur(10px);
    -o-filter: blur(10px);
    height: auto;
    left: 0;
    padding-bottom: 100%;
    right: 0;
    top: 0;
    vertical-align: top;
    width: 100%;
}

// .device-desktop .checkPreview{ height:}
@media screen and (max-width: 992px) {
    .ts_csl_post_slick-next{
        right:-13px;
    }

    .ts_csl_post_slick-prev{
        left: -13px;
    }
}
  
@keyframes pulse-dark {
	0% {
		transform: scale(.85);
		box-shadow: 0 0 0 0 rgba(0, 0, 0, .7)
	}
	70% {
		transform: scale(1)
	}
	99% {
		box-shadow: 0 0 0 8px rgba(0, 0, 0, 0)
	}
	100% {
		transform: scale(.85);
		box-shadow: 0 0 0 0 rgba(0, 0, 0, 0)
	}
}