.tb_post_modal_modal{
    font-family: 'Inter', sans-serif;
    font-size: 14px;
    line-height: 1.42857;
    color: #262626;
}

.tb_post_modal_modal {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    overflow-y: auto;
    overflow-x: hidden;
    z-index: 10000;
    opacity: 0;
    background: rgba(0, 0, 0, 0.7);
    -webkit-transition: opacity 0.3s ease-out 0s;
    -moz-transition: opacity 0.3s ease-out 0s;
    -ms-transition: opacity 0.3s ease-out 0s;
    -o-transition: opacity 0.3s ease-out 0s;
    transition: opacity 0.3s ease-out 0s;
}
.tb_post_modal_modal.tb_post_modal_show{
    opacity: 1;
}

.tb_post_modal_modal.show {
  display: block;
  opacity:1;
  visibility: visible;
  transition: opacity 300ms ease-in-out;
}
.tb_post_skeleton-box{
    width:100%;
    height: 100%;
}

.sk_loader_product, .sk_loader_product_thumb {
    width: 100%;
    height: 100%;
    position: relative;
    background: #d9d9d9;
    background-color: #d9d9d9!important;
    overflow: hidden;
}

.sk_loader_product:before, .sk_loader_product_thumb:before{
    position: absolute;
    content: "";
    height: 100%;
    width: 100%;
    left:0;
    background-image: linear-gradient(to right, #d9d9d9 0%, rgba(0,0,0,0.05) 20%, #d9d9d9 40%, #d9d9d9 100%);
    background-repeat: no-repeat;
    background-size: 450px 400px;
    animation: shimmer 0.8s linear infinite;
}

.sk_loader_product:before{
    background-image: linear-gradient(to right, #d9d9d9 0%, rgba(0, 0, 0, 0.05) 30%, #d9d9d9 60%, #d9d9d9 100%);
}

.sk_loader_product_thumb:before{
    background-size: 650px 600px;
}

.sk_loader_product:before {
    background-size: 100% 100%;
}

@keyframes shimmer {
    0%{
      background-position: -450px 0;
    }
    100%{
      background-position: 450px 0;
    }
}

.tb_post_modal_fade {
    -webkit-transition: opacity 0.15s linear;
    transition: opacity 0.15s linear;
}

.tb_post_modal_modal_dialog {
    -webkit-transition: -webkit-transform 0.3s ease-out;
    transition: -webkit-transform 0.3s ease-out;
    transition: transform 0.3s ease-out;
    transition: transform 0.3s ease-out, -webkit-transform 0.3s ease-out;
    min-height: calc(100% - 3.5rem);
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: center;
    -ms-flex-align: center;
    align-items: center;
    position: relative;
    width: 100%;
    padding: 20px 60px;
    max-width: 100%;
    z-index: 1;
    -moz-transition-property: -moz-transform,-webkit-transform,-moz-transform,-ms-transform,opacity;
    -o-transition-property: -o-transform,-webkit-transform,-moz-transform,-ms-transform,opacity;
    -webkit-transition-property: -webkit-transform,-webkit-transform,-moz-transform,-ms-transform,opacity;
    transition-property: transform,-webkit-transform,-moz-transform,-ms-transform,opacity;
    -moz-transition-duration: .2s;
    -o-transition-duration: .2s;
    -webkit-transition-duration: .2s;
    transition-duration: .2s;
    -moz-transition-timing-function: ease-in;
    -o-transition-timing-function: ease-in;
    -webkit-transition-timing-function: ease-in;
    transition-timing-function: ease-in;
    transition: all ease .3s;
    -moz-transition: all ease .3s;
    -webkit-transition: all ease .3s;
    -o-transition: all ease .3s;
    -webkit-transform: scale(0.8);
    -moz-transform: scale(0.8);
    -ms-transform: scale(0.8);
    transform: scale(0.8);
}

