.ts_gs_post_container{
    width: 100%;
}

.ts_gs_post_wrapper2xleft {
    float: left;
    padding: 7.5px;
}

.ts_gs_post_wrapper2xright {
    float: right;
    padding: 7.5px;
}

.ts_cs_post_row {
    width: 100%;
}

.ts_gs_post_wrapper{
    width: 20%;
    padding: 7.5px;
    float: left;
}

.ts_gs_social_rating{
    height: 26px;
    width: 125px;
    background-repeat: no-repeat;
}

.ts_gs_button{
    color: rgb(255, 255, 255);
    border: 1px solid rgb(0, 0, 0); 
    padding: 5px;
    font-family: Inter;
    //border-radius: 3px
}

.ts_gs_post_in {
    overflow: hidden;
    position: relative;
    cursor:pointer;
}
.tb_gs_slider_next_arrow-next, .tb_gs_slider_pre_arrow-prev {
    border-radius: 3px;
    -webkit-border-radius: 3px;
    -moz-border-radius: 3px;
    background-color: #fff;
    opacity: .5;
    cursor: pointer;
    color: transparent;
    width: 44px;
    height: 54px;
    overflow: hidden;
    top: 50%;
    margin-top: -27px;
    position: absolute;
    z-index: 9;
    display: flex;
    align-items: center;
    justify-content: center;
    border: 0;
}

.splide__arrow--prev.tb_gs_slider_pre_arrow-prev{
    left: 0px;
    font-size: 28px;
    color: #fff; 
}

.tb_gs_slider_next_arrow-next {
    right: 0px;
    font-size: 28px;
    color: #fff;
}
.ts_cs_post_large{
    width: 50%; 
    float: left;
}

.ts_cs_post_small{
    width: 50%; 
    float: left;
}

.ts_gs_image {
    position: relative;
    display: block;
    max-width: 100%;
    max-height: 100%;
    width: 100%;
    object-fit: cover;
    object-position: center;
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    background-repeat: no-repeat;
    background-size: cover;
}

.ts_gs_image_blur_bg {
    background-position: 50%;
    background-repeat: no-repeat;
    background-size: cover;
    height: 100%;
    left: 0;
    padding-bottom: 100%;
    right: 0;
    top: 0;
    width: 100%;
}

.ts_gs_post_in:hover .ts_gs_social_warpper{
    opacity: 1;
}

.ts_gs_social_warpper {
    position: absolute;
    width: 100%;
    height: 100%;
    left: 0;
    top: 0;
    right:0;
    bottom:0;
    opacity: 0;
    z-index: 1;
    background-color: rgba(0,0,0,.8);
    transition: .2s linear;
    -webkit-transition: .2s linear;
}

.ts_gs_social_wrappeer_in {
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    -ms-transform: translate(-50%, -50%);
    -moz-transform: translate(-50%, -50%);
    -webkit-transform: translate(-50%, -50%);
}

.ts_gs_social_ {
    font-size: 30px;
    text-align: center;
    
}
.ts_gs_image_blur_bg {
    height: auto;
    width: 100%;
    padding-bottom: 100%;
    background-size: cover;
    background-repeat: no-repeat;
    background-position: 50%;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    vertical-align: top;
    filter: blur(30px);
    -webkit-filter: blur(10px);
    -moz-filter: blur(10px);
    -ms-filter: blur(10px);
    -o-filter: blur(10px);
}

.ts_gs_button {
    color: #fff;
    border: 1px solid #fff;
    //border-radius: 4px;
    padding: 10px 15px;
    font-weight: 400;
    font-style: normal;
    white-space: nowrap;
    text-overflow: ellipsis;
    overflow: hidden;
    font-size: 14px;
    margin-top: 15px;
    text-transform: uppercase;
    -moz-transition: background 0.4s, color 0.4s;
    transition: background 0.4s, color 0.4s;

}

.ts_gs_button:hover {
    background: rgba(255,255,255,1);
    color: #000;
    font-weight:500;
}

.ts_gs_post_wrapper2xleft .ts_gs_image, .ts_gs_post_wrapper2xright .ts_gs_image {
    height: 1015px !important;
}

.ts_st_post_hotspot{
    width: 100%;
    height: 100%;
    position: absolute;
    z-index: 4;
    top: 50%;
    transform: translateY(-50%);
    left: 0;
    right: 0;
    margin: 0 auto;
}

.ts_st_post_hotspot_icon{
    position: absolute;
    cursor: pointer;
    display: flex;
    left:50%;
    top:50px;
}

.ts_st_post_hotspot_icon_blob{
    border-radius: 50%;
    margin: 0;
    height: 16px;
    width: 16px;
    transform: scale(1);
    background: #ffffff;
    box-shadow: 0 0 0 0 rgba(0, 0, 0, 1);
    animation: pulse-dark 2s infinite;
    display: inline-block;
}

.tb_gst_post_container .splide__arrow--next.tb_gs_slider_next_arrow-next,
.tb_gst_post_container .splide__arrow--prev.tb_gs_slider_pre_arrow-prev{
    background: #fff;
    opacity: 0.8;
    width: 44px;
    height: 74px;
    box-shadow: 0px 0px 5px  rgba(0, 0, 0, 0.2);
    color:#000;
}

.tb_gst_post_container .splide__arrow--next.tb_gs_slider_next_arrow-next{
    right:0;
    border-radius: 3px 0 0 3px; 
}

.tb_gst_post_container button.splide__arrow--prev.tb_gs_slider_pre_arrow-prev{
    left:0;
    border-radius: 0 3px 3px 0;
}

.tb_gst_post_container .splide__arrow--next.tb_gs_slider_next_arrow-next .tb__icon.tb-chevron-right,
.tb_gst_post_container button.splide__arrow--prev.tb_gs_slider_pre_arrow-prev .tb__icon.tb-chevron-left{
    color:#525252;
}
.ts_gs_image img{
    margin: 0px;
    padding: 0px;
}
@keyframes pulse-dark {
	0% {
		transform: scale(.85);
		box-shadow: 0 0 0 0 rgba(0, 0, 0, .7)
	}
	70% {
		transform: scale(1)
	}
	99% {
		box-shadow: 0 0 0 8px rgba(0, 0, 0, 0)
	}
	100% {
		transform: scale(.85);
		box-shadow: 0 0 0 0 rgba(0, 0, 0, 0)
	}
}

@media screen and (max-width:767px){
    .ts_gs_post_in:hover .ts_gs_social_warpper{
        display: none;
    }


    .tb_gst_post_container .splide__arrow--next.tb_gs_slider_next_arrow-next, 
    .tb_gst_post_container .splide__arrow--prev.tb_gs_slider_pre_arrow-prev{
        width: 34px;
        height: 44px;
        font-size: 20px;
        opacity: 0.8;
    }
}