body {
  margin: 0;
  padding: 0;
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

#taggbox_main *,
#taggbox_main *::before,
#taggbox_main *::after {
  box-sizing: border-box;
}

#taggbox_main {
  font-family: "Inter";
  line-height: 1.5;
  font-weight: 400;
}

#taggbox_main a {
  text-decoration: none;
}

.alert-enter {
  opacity: 0;
  transform: scale(0.9);
}

.alert-enter-active {
  opacity: 1;
  transform: translateX(0);
  transition: opacity 300ms, transform 300ms;
}

.alert-exit {
  opacity: 1;
}

.alert-exit-active {
  opacity: 0;
  transform: scale(0.9);
  transition: opacity 300ms, transform 300ms;
}

.postContentCard a:hover {
  text-decoration: underline !important;
}

