.ts__share-icon_wrapper {
  position: absolute;
  top: 15px;
  left: 15px;
  opacity: 0;
  visibility: hidden;
  -webkit-backface-visibility: hidden;
  transition: all 0.1s ease 0s;
  z-index: 1;
}

.ts__share-icon_wrapper a {
  display: flex;
  align-items: center;
  justify-content: center;
  background: #fff;
  cursor: pointer;
  font-size: 16px !important;
  width: 30px;
  height: 30px;
  line-height: 30px !important;
  text-decoration: none !important;
  text-align: center;
  position: relative;
  border-radius: 30px;
  -moz-border-radius: 30px;
  -webkit-border-radius: 30px;
  box-shadow: 0 1px 3px rgba(169, 167, 167, 0.5);
  -moz-box-shadow: 0 1px 3px rgba(169, 167, 167, 0.5);
  -webkit-box-shadow: 0 1px 3px rgba(169, 167, 167, 0.5);
  transition: all 0.2s ease;
  color: #333;
}

.ts__share-icon_wrapper a {
  position: absolute;
  -webkit-transform: translateZ(0);
  transform: translateZ(0);
  transition: all 0.3s ease;
}

.ts__share-icon_wrapper a:first-child {
  z-index: -1;
}

.ts__share-icon_wrapper a:nth-child(2) {
  z-index: -2;
}

.ts__share-icon_wrapper a:nth-child(3) {
  z-index: -3;
}

.ts__share-icon_wrapper a:nth-child(4) {
  z-index: -4;
}

.tb__post_in:hover .ts__share-icon_wrapper,
.tb__post_in:hover .ts__share-icon_wrapper {
  opacity: 1;
  visibility: visible;
  top: 10px;
}

.tb__post_in:hover .ts__share-icon_wrapper:hover a,
.tb__post_in:hover .ts__share-icon_wrapper:hover a {
  opacity: 1;
  position: relative !important;
}

.tb__post_in .ts__share-icon_wrapper:hover a:nth-child(2),
.tb__post_in .ts__share-icon_wrapper:hover a:nth-child(2) {
  -webkit-transform: translate3d(0, 10px, 0);
  transform: translate3d(0, 10px, 0);
  left: 0;
}

.tb__post_in .ts__share-icon_wrapper:hover a:nth-child(3),
.tb__post_in .ts__share-icon_wrapper:hover a:nth-child(3) .tb__post_in .ts__share-icon_wrapper:hover a:nth-child(3),
.tb__post_in .ts__share-icon_wrapper:hover a:nth-child(3) {
  -webkit-transform: translate3d(0, 20px, 0);
  transform: translate3d(0, 20px, 0);
  left: 0;
}

.tb__post_in .ts__share-icon_wrapper:hover a:nth-child(4),
.tb__post_in .ts__share-icon_wrapper:hover a:nth-child(4) {
  -webkit-transform: translate3d(0, 30px, 0);
  transform: translate3d(0, 30px, 0);
  left: 0;
}

.tb_sc_post_wrapper:hover .ts__share-icon_wrapper,
.tb_sc_post_wrapper:hover .ts__share-icon_wrapper {
  opacity: 1;
  visibility: visible;
}

.tb_sc_post_wrapper:hover .ts__share-icon_wrapper:hover a,
.tb_sc_post_wrapper:hover .ts__share-icon_wrapper:hover a {
