}

.tb_sc_post_wrapper .ts__share-icon_wrapper:hover a:nth-child(3),
.tb_sc_post_wrapper .ts__share-icon_wrapper:hover a:nth-child(3) .tb_sc_post_wrapper .ts__share-icon_wrapper:hover a:nth-child(3),
