.ts_cp_post_container{
    display: flex;
    flex-wrap: wrap;
    width: 100%;
}

.ts_cp_post_wrapper{
    width: 25%;
    padding: 7.5px;
}

.ts_cp_post_in{
    display: block;
    position: relative;
    box-shadow: 0 0 1.5px rgba(0, 0, 0, 0.20);
    cursor: pointer;
}

.ts_cp_image {
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
}

.ts_cp_author_wrapper {
    padding: 10px;
}

.ts_cp_author {
    width: calc(100% - 30px);
}
.tb_cp_image img{
margin: 0px;
padding: 0px;
}

.ts_cp_author_profile {
    height: 44px;
    width: 44px;
    border-radius: 50%;
    margin-right: 10px;
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
    display: flex;
    flex-shrink: 0;
    overflow: hidden;
}

.ts_cp_authorname {
    font-size: 14px;
    font-family: "Inter";
    font-weight: 600;
    margin-bottom: 4px;
    color: #2b2b2b;
    white-space: nowrap;
    display: block;
    overflow: hidden;
    text-overflow: ellipsis;
}

.ts_cp_author {
    display: flex;
    align-items: center;
    flex-grow: 1;
}

.ts_cp_author_wrapper {
    display: flex;
    align-items: center;
    justify-content: end;
}

.ts_cp_author_info {
    overflow: hidden;
    display: flex;
    flex-shrink: 0;
    flex-direction: column;
    width: calc(100% - 54px);
    padding-right: 10px;
}

.ts_cp_post_info {
    display: flex;
    align-items: center;
    font-family: "inter";
}

.ts_cp_seprator {
    height: 4px;
    width: 4px;
    background-color: #2b2b2b;
    border-radius: 50%;
    margin: 0 6px;
    opacity: 0.8;
    display: flex;
    flex-shrink: 1;
}

.ts_cp_username, .ts_cp_time {
    font-size: 10px;
    color: #2b2b2b;
    font-weight: 500;
    opacity: 0.8;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
}

.ts_cp_username{
    max-width: 65%;
}

.ts_cp_time{
    max-width: 35%;
}

.ts_cp_social_ {
    font-size: 24px;
}

.ts_cp_video {
    width: 100%;
}

.ts_cp_post_iocn {
    position: absolute;
    width: 100%;
    height: auto;
    top: 0;
    left: 0;
    right: 0;
    text-align: center;
    padding: 12px;
    z-index: 99;
    display: block;
}

.ts_cp_slider-social-icon {
    font-size: 40px;
    margin-bottom: 10px;
}

.ts_cp_video_icon {
    font-size: 14px;
    float: left;
    color: #fff;
}

.ts_cp_shop_icon {
    font-size: 14px;
    float: right;
    color: #fff;
}

.tb__icon.tb-bag {
    font-size: 17px;
}
.ts_cp_card_socialAction  {
    color: #333;
    display: flex;
    align-items: center;
    width: 100%;
    justify-content: end;
    background: rgba(0,0,0,.04);
    padding: 3px 8px;
}

.ts_cp_card_socialAction a {
    text-decoration: none !important;
    vertical-align: middle;
    font-size: 16px;
    padding: 5px 2px;
    color: #333;
    text-align: center;
    color: inherit;
    transition: all .3s ease;
}
.ts_cp_card_socialAction a .tb__icon {
    color: rgb(128, 128, 128);
    width: 20px;
}
.ts_cp_card_socialAction a svg {
    width: 20px;
}
.ts_cp_postContentCard_ratting {
    padding: 10px;
}