.tb_st_post_container{
    // display: flex;
    // flex-wrap: wrap;
    width: 100%;
}

.tb_st_post_wrapper_outer {
    position: relative;
    width: 100%;
}

.tb_st_post_wrapper {
    width: 100%;
    margin: 0 auto;
    position: relative;
    // padding-bottom: 200%;
    // height: 100%;
    border-radius: calc(1.5vw + 5px);
    overflow: hidden;
    background-color: #fff;
    aspect-ratio: 1/2; 
}

.tb_st_post_in {
    position: absolute;
    height: 100%;
    left: 0;
    right: 0;
    width: 100%;
    opacity: 0.5;
    top: 0;
    bottom: 0;
}
.swiper-slide-active .tb_st_post_in {
    opacity: 1;
}
.tb_theme_container.thm_61 .swiper-slide {
    height: auto;
}

.ts_st_image_blur_bg {
    position: absolute;
    height: 100%;
    width: 100%;
    padding-bottom: 100%;
    background-size: cover;
    background-repeat: no-repeat;
    background-position: 50%;
    z-index: 0;
    top: 0;
    left: 0;
    right: 0;
    filter: blur(5px);
    -moz-filter: blur(5px);
    -webkit-filter: blur(5px);
    -ms-filter: blur(5px);
    -o-filter: blur(5px);
}

.tb_st_media_wrap {
    position: absolute;
    height: 100%;
    left: 0;
    right: 0;
    width: 100%;
    //background-color: #fff;
    top: 0;
    bottom: 0;
}

.ts_st_media_wrap_video {
    position: absolute;
    height: 100%;
    left: 0;
    right: 0;
    width: 100%;
    background-color: #fff;
}

.tb_st_image {
    max-width: 100%;
    display: block;
    height: 100%;
    width: 100%;
    object-fit: cover;
    object-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
    z-index: 1;
    position: relative;
}
