.tb_st_image_hotspot {
    display: block;
    max-width: 100%;
    max-height: 100%;
    width: 100%;
    object-fit: cover;
    object-position: center;
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    background-repeat: no-repeat;
    background-size: cover;
}
.tb_st_image_hotspot img{
    margin: 0px;
    padding: 0px;
}
.tb_st_image img{
    margin: 0px;
    padding: 0px; 
}
.ts_shopping_hover{font-size: 18px;    left: 8px; position: absolute; color:#fff}
.ts_st_video {
    max-width: 100%;
    display: block;
    height: 100%;
    width: 100%;
    object-fit: cover;
    object-position: center;
}
.ts_shopping_hover{font-size: 18px;    left: 8px; position: absolute; color:#fff}
.tb_st_author_wrapper {
    padding: 10px;
}

.tb_st_author {
    width: calc(100% - 30px);
}

.tb_st_author_profile {
    height: 35px;
    width: 35px;
    border-radius: 50%;
    margin-right: 10px;
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
    display: flex;
    flex-shrink: 0;
    overflow: hidden;
}
.tb_st_author_profile img{ width: 100%; height: 100%;}
img.ts_st_playIcon {
    position: absolute;
    top: 49%;
    left: 43%;
    width: 40px;
}
.tb_st_authorname {
    font-size: 14px;
    font-weight: 600;
    color: #fff;
    white-space: nowrap;
    display: block;
    overflow: hidden;
    text-overflow: ellipsis;
}

.tb_st_author {
    display: flex;
    align-items: center;
    flex-grow: 1;
}

.tb_st_author_wrapper {
    position: absolute;
    bottom: 20px;
    width:100%;
    z-index: 9;
}

.tb_st_author_info {
    overflow: hidden;
    display: flex;
    flex-shrink: 0;
    flex-direction: column;
    width: calc(100% - 54px);
    padding-right: 10px;
}

.tb_st_post_info {
    display: flex;
    align-items: center;
}

.tb_st_seprator {
    height: 4px;
    width: 4px;
    background-color: #fff;
    border-radius: 50%;
    margin: 0 6px;
    opacity: 0.8;
    display: flex;
    flex-shrink: 1;
}

.tb_st_username, .tb_st_time {
    font-size: 10px;
    color: #fff;
    font-weight: 500;
    opacity: 0.8;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
}

.tb_st_username{
    max-width: 65%;
}

.tb_st_time{
    max-width: 35%;
}

.tb_st_social_ {
    font-size: 37px;
    margin-bottom: 5px;
}

.tb_theme_container.thm_61 {
    position: relative;
}

.ts_st_shop_icon {
    background: rgba(255,255,255,0.6);
    width: 35px;
    height: 35px;
    border-radius: 36px;
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;
    margin-bottom: 10px;
    transition: all 0.3s;
    
}

.ts_st_shop_icon svg {
    position: absolute;
    left: 8px;
}

.tb_st_shop_hover {
    position: absolute;
    white-space: nowrap;
    left: 44px;
    color: #fff;
    font-size: 13px;
    top: 50%;
    transform: translate(0, -50%);
    width: 0px;
    transition: all 0.3s;
    overflow: hidden;
}
.ts_st_shop_icon:hover .tb_st_shop_hover {
    width: 100%;
}
.tb_st_image img {
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
}
.ts_st_shop_icon:hover {
    width: 60%;
    justify-content: space-between;
    transition: all 0.3s;
}

.ts_st_shop_icon:hover .tb_st_shop_hover {
    width: 100%;
}
.tb_st_image img {
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
}

.ts_st_shop_icon:hover .ts_shopping_hover {
    width: auto;
    transition: all 0.3s;
}


.tb_st_social_ {
    margin-bottom: 5px;
    width: 35px;
    height: 35px;
    background: rgba(255, 255, 255, 0.6);
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 20px;
}

.ts_st_post_hotspot{
    width: 100%;
    height: 100%;
    position: absolute;
    z-index: 4;
    top: 50%;
    transform: translateY(-50%);
    left: 0;
    right: 0;
    margin: 0 auto;
}

.ts_st_post_hotspot_icon{
    position: absolute;
    cursor: pointer;
    display: flex;
    left:50%;
    top:50px;
}

.ts_st_post_hotspot_icon_blob{
    border-radius: 50%;
    margin: 0;
    height: 16px;
    width: 16px;
    transform: scale(1);
    background: #ffffff;
    box-shadow: 0 0 0 0 rgba(0, 0, 0, 1);
    animation: pulse-dark 2s infinite;
    display: inline-block;
}

.sw_slider_prev_arrow,
.sw_slider_next_arrow {
  margin-top: 0px;
  width: 30px;
  height: 30px;
  z-index: 2;
  margin: 0 auto;
  left: 0;
  top: calc(50% - 25px);
  right: 0;
  position: absolute;
  display: flex;
  align-items: center;
  cursor: pointer;
  background: rgba(255,255,255,0.7);
  border-radius: 50px;
  padding: 10px;
  color: rgba(0,0,0,0.7);
}


.sw_slider_prev_arrow svg,
.sw_slider_next_arrow svg {
  width: 100%;
  height: 100%;
  display: inline-block;
  /* opacity: .8; */
}

