.ts_st_left_arrow_17{left: -16%; right: 0;}
.ts_st_left_arrow_20{left: -19%; right: 0;}
.ts_st_left_arrow_25{left: -24%; right: 0;}
.ts_st_left_arrow_34{left: -32%; right: 0;}
.ts_st_left_arrow_90{left: -90%; right: 0;}

.ts_st_right_arrow_17{right: -16%;  left: 0;}
.ts_st_right_arrow_20{right: -19%;  left: 0;}
.ts_st_right_arrow_25{right: -24%;  left: 0;}
.ts_st_right_arrow_34{right: -32%;  left: 0;}
.ts_st_right_arrow_90{right: -90%;  left: 0;}

.sw_slider_prev_arrow:before,
.sw_slider_next_arrow:before {
  font-size: inherit;
  margin-left: -16px;
  margin-top: -16px;
  color: #e8e8e8;
}

.tb_st_tb_multiple_ico {
    z-index: 1;
    position: absolute;
    right: 19px;
    top: 13px;
    color: #fff;
}
@keyframes pulse-dark {
	0% {
		transform: scale(.85);
		box-shadow: 0 0 0 0 rgba(0, 0, 0, .7)
	}
	70% {
		transform: scale(1)
	}
	99% {
		box-shadow: 0 0 0 8px rgba(0, 0, 0, 0)
	}
	100% {
		transform: scale(.85);
		box-shadow: 0 0 0 0 rgba(0, 0, 0, 0)
	}
}









