.ts_cs_post_container{
    height: inherit;
    width: 94%;
    margin: 0px auto;
    padding: 6px;
}

.ts_cs_post_in {
    overflow: hidden;
    border-radius: 100%;
    position: relative;
    border: 1px solid #f8f8f8;
    cursor: pointer;
    -moz-aspect-ratio: 1/1;
    -webkit-aspect-ratio: 1/1;
    -ms-aspect-ratio: 1/1;
    -o-aspect-ratio: 1/1;
    aspect-ratio: 1/1;
}

.ts_cs_media_wrap{
    -moz-aspect-ratio: 1/1;
    -webkit-aspect-ratio: 1/1;
    -ms-aspect-ratio: 1/1;
    -o-aspect-ratio: 1/1;
    aspect-ratio: 1/1;
}

.ts_cs_post_in:hover .ts_cs_social_warpper{
    opacity: 1;
    z-index: 9;
}

.ts_cs_social_warpper {
    position: absolute;
    width: 100%;
    height: 100%;
    left: 0;
    top: 0;
    right:0;
    bottom:0;
    opacity: 0;
    background-color: rgba(0,0,0,.8);
    transition: .2s linear;
    -webkit-transition: .2s linear;
}

.ts_cs_social_wrappeer_in {
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    -ms-transform: translate(-50%, -50%);
    -moz-transform: translate(-50%, -50%);
    -webkit-transform: translate(-50%, -50%);
}

.ts_cs_social_ {
    font-size: 24px;
    text-align: center;
}

.ts_cs_button {
    color: #fff;
    border: 1px solid #fff;
    border-radius: 4px;
    padding: 5px;
    font-weight: 400;
    font-style: normal;
    white-space: nowrap;
    text-overflow: ellipsis;
    overflow: hidden;
    font-size: 14px;
    margin-top: 10px;
    cursor: pointer;
}

.ts_cs_social_rating{
    height: 26px;
    width: 125px;
    background-repeat: no-repeat;
}

.ts_cs_post_iocn {
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    -ms-transform: translate(-50%, -50%);
    -moz-transform: translate(-50%, -50%);
    -webkit-transform: translate(-50%, -50%);
    transition: all 0.2s;
    -ms-transition: all 0.2s;
    -moz-transition: all 0.2s;
    -webkit-transition: all 0.2s;
}
    
.ts_cs_post_in:hover .ts_cs_post_iocn {
    opacity: 0;
}

.ts_st_post_hotspot{
    width: 100%;
    height: 100%;
    position: absolute;
    z-index: 4;
    top: 50%;
    transform: translateY(-50%);
    left: 0;
    right: 0;
    margin: 0 auto;
}

.ts_st_post_hotspot_icon{
    position: absolute;
    cursor: pointer;
    display: flex;
    left:50%;
    top:50px;
}

.ts_st_post_hotspot_icon_blob{
    border-radius: 50%;
    margin: 0;
    height: 16px;
    width: 16px;
    transform: scale(1);
    background: #ffffff;
    box-shadow: 0 0 0 0 rgba(0, 0, 0, 1);
    animation: pulse-dark 2s infinite;
    display: inline-block;
}

.ts_cs_image {
    aspect-ratio: 1/1;
    overflow: hidden;
    border-radius: 100%;
}
.ts_cs_image img{
    margin: 0px;
    padding: 0px;
}

.ts_cs_img__ {
    width: 100%;
    height: 100%;
    object-fit: cover;
    -moz-aspect-ratio: 1/1;
    -webkit-aspect-ratio: 1/1;
    -ms-aspect-ratio: 1/1;
    -o-aspect-ratio: 1/1;
    aspect-ratio: 1/1;
    display: block;
    border-radius: 100%;
}

.ts_cs_post_in .ts_post_iocn{
    height: 100%;
}

.ts_cs_post_in .ts_post_iocn .ts_video_icon {
    margin: 0 10px;
}

.ts_cs_post_in:hover .ts_post_iocn .ts_social-icon .ts_video_icon svg{
    display: none;
}

.ts_social-icon.tb_cs_video_multiImage {
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
}

@keyframes pulse-dark {
	0% {
		transform: scale(.85);
		box-shadow: 0 0 0 0 rgba(0, 0, 0, .7)
	}
	70% {
		transform: scale(1)
	}
	99% {
		box-shadow: 0 0 0 8px rgba(0, 0, 0, 0)
	}
	100% {
		transform: scale(.85);
		box-shadow: 0 0 0 0 rgba(0, 0, 0, 0)
	}
}

.tb_cs_slider_next_arrow-next, .tb_cs_slider_pre_arrow-prev {
    border-radius: 3px;
    -webkit-border-radius: 3px;
    -moz-border-radius: 3px;
    background-color: #fff;
    opacity: .8;
    cursor: pointer;
    color: transparent;
    height: 45px;
    width: 35px;
    overflow: hidden;
    top: 50%;
    margin-top: -27px;
    position: absolute;
    display: flex;
    align-items: center;
    justify-content: center;
    border: 0;
}

.tb_cs_slider_next_arrow-next,
.tb_cs_slider_pre_arrow-prev {
    box-shadow: 0 0 3px rgba(0, 0, 0, 0.6);
    color: #000;
    font-size: 22px;
}

.tb_cs_slider_next_arrow-next.splide__arrow--next {
    right: -10px;
}

.tb_cs_slider_pre_arrow-prev.splide__arrow--prev {
    left: -10px;
}

@media screen and (max-width:767px){
    .ts_cs_post_in:hover .ts_cs_social_warpper{
        display: none;
    }

    .ts_cs_post_in:hover .ts_post_iocn .ts_social-icon .ts_video_icon svg{
        display: block;
    }
}