.ts_ms_post_container{
    display: block;
    flex-wrap: wrap;
    width: 100%;
}

.ts_ms_post_wrapper{
    width: 25%;
    padding: 7.5px;
}

.ts_ms_post_in{
    display: block;
    box-shadow: 0 0 1.5px rgba(0, 0, 0, 0.20);
    overflow: hidden;
    cursor: pointer;
}

.ts_ms_image {
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
}

.ts_ms_author_wrapper {
    padding: 10px;
}

.ts_ms_author {
    width: calc(100% - 30px);
}

.ts_ms_author_profile {
    height: 44px;
    width: 44px;
    border-radius: 50%;
    margin-right: 10px;
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
    display: flex;
    flex-shrink: 0;
}

.ts_ms_authorname {
    font-size: 14px;
    font-weight: 600;
    margin-bottom: 4px;
    color: #2b2b2b;
    white-space: nowrap;
    display: block;
    overflow: hidden;
    text-overflow: ellipsis;
}

.ts_ms_author {
    display: flex;
    align-items: center;
    flex-grow: 1;
}

.ts_ms_author_wrapper {
    display: flex;
    align-items: center;
}

.ts_ms_author_info {
    overflow: hidden;
    display: flex;
    flex-shrink: 0;
    flex-direction: column;
    width: calc(100% - 54px);
    padding-right: 10px;
}

.ts_ms_post_info {
    display: flex;
    align-items: center;
}

.ts_ms_seprator {
    height: 4px;
    width: 4px;
    background-color: #2b2b2b;
    border-radius: 50%;
    margin: 0 6px;
    opacity: 0.8;
    display: flex;
    flex-shrink: 1;
}

.ts_ms_username, .ts_ms_time {
    font-size: 10px;
    color: #2b2b2b;
    font-weight: 500;
    opacity: 0.8;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
}

.ts_ms_username{
    max-width: 65%;
}

.ts_ms_time{
    max-width: 35%;
}

.ts_ms_social_ {
    font-size: 24px;
}

.ts_ms_post_in {
    position: relative;
}

.ts_ms_Post_hover {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    opacity: 0;
    z-index: 1;
    transition: .2s linear;
    -moz-transition: .2s linear;
}

.ts_ms_card_content_00 {
    padding: 5%;
}

.ts_ms_card_hover_content {
    position: absolute;
    bottom: 50%;
    left: 50%;
    right: auto;
    text-align: center;
    width: 100%;
    padding: 10px;
    -webkit-transform: translate(-50%,50%);
    transform: translate(-50%,50%);
}

.ts_ms_card_content{
    display: -webkit-box;
    -webkit-line-clamp: 3;
    -webkit-box-orient: vertical;
    overflow: hidden;
    text-align: center;
    line-height: 1.5;

    display: -webkit-box;
    -webkit-line-clamp: 3;
    -webkit-box-orient: vertical;
    overflow: hidden;
    text-align: center;
    line-height: 1.5;
}

.ts_ms_card_hover_overlay {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    display: block;
    width: 100%;
    height: 100%;
    background-color: #fff;
    opacity: .8;
    transition: background 0.5s, color 0.5s;
    -moz-transition: background 0.5s, color 0.5s;
    -ms-transition: background 0.5s, color 0.5s;
    -o-transition: background 0.5s, color 0.5s;
    -webkit-transition: background 0.5s, color 0.5s;
}

.ts_ms_Post_hover:hover{
    opacity: 1;
}

.ts_ms_slider-social-icon {
    font-size: 40px;
    margin-bottom: 10px;
}

.ts_ms_shop_btn {
    border-radius: 0px;
    padding: 9px 15px;
    display: inline-block;
    text-decoration: none;
    white-space: nowrap;
    text-overflow: ellipsis;
    overflow: hidden;
    max-width: 100%;
    font-size: 14px;
    transition: background 0.5s, color 0.5s;
    -moz-transition: background 0.5s, color 0.5s;
    -ms-transition: background 0.5s, color 0.5s;
    -o-transition: background 0.5s, color 0.5s;
    -webkit-transition: background 0.5s, color 0.5s;
}

.ts_ms_card_shop_btn.ts_black_bg_hover .ts_ms_shop_btn {
    background: rgba(255,255,255,0);
    color: #fff;
    border: 1px solid #fff;
    font-weight: 500;
}

.ts_ms_card_shop_btn.ts_white_bg_hover .ts_ms_shop_btn {
    background: rgba(0,0,0,1);
    color: #fff;
    border: 1px solid #000;
}

.ts_ms_card_shop_btn.ts_black_bg_hover .ts_ms_shop_btn:hover {
    background: rgba(255,255,255,1);
    color: #000;
    border: 1px solid #fff;
}

.ts_ms_post_image {
    position: relative;
    display: block;
    overflow: hidden;
    border-radius: 3px;
    cursor: pointer;
    padding-bottom: 100%;
}

.ts_ms_media_wrap {
    position: absolute;
    left: 0;
    right: 0;
    margin: 0 auto;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
    height: 100%;
}

.ts_ms_image_blur_bg {
    position: absolute;
    height: auto;
    width: 100%;
    padding-bottom: 100%;
    background-size: cover;
    background-repeat: no-repeat;
    background-position: 50%;
    z-index: 0;
    top: 0;
    left: 0;
    right: 0;
    filter: blur(5px) ;
    -moz-filter: blur(5px) ;
    -webkit-filter: blur(5px) ;
    -ms-filter: blur(5px) ;
    -o-filter: blur(5px) ;
    
}

.ts_ms_media_wrap_video {
    background: #000;
    position: absolute;
    left: 0;
    right: 0;
    margin: 0 auto;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
    height: 100%;
}

.ts_ms_image {
    position: absolute;
    display: block;
    max-width: 100%;
    max-height: 100%;
    width: 100%;
    height: 100%;
    object-fit: cover;
    object-position: center;
}
.ts_ms_image img{
    margin: 0px;
    padding: 0px;
}
.ts_ms_video {
    height: 460px;
}

.ts_ms_slider-social-icon:after {
    display: block;
    clear: both;
    content: "";
}

.ts_ms_card_socialAction  {
    color: #333;
    bottom: 0;
    position: absolute;
    display: flex;
    align-items: center;
    width: 100%;
    justify-content: flex-end;
    background: none;
    left: 0;
}

.ts_ms_card_socialAction a {
    text-decoration: none;
    vertical-align: middle;
    font-size: 16px;
    padding: 5px 0;
    width: 26px;
    text-align: center;
    color: inherit;
}

.ts_ms_social_rating{
    height: 26px;
    width: 125px;
    margin: auto;
    background-repeat: no-repeat;
}

.ts_card_hover_in {
    height: 100%;
}

.ts_st_post_hotspot{
    width: 100%;
    height: 100%;
    position: absolute;
    z-index: 4;
    top: 50%;
    transform: translateY(-50%);
    left: 0;
    right: 0;
    margin: 0 auto;
}

.ts_st_post_hotspot_icon{
    position: absolute;
    cursor: pointer;
    display: flex;
    left:50%;
    top:50px;
}

.ts_st_post_hotspot_icon_blob{
    border-radius: 50%;
    margin: 0;
    height: 16px;
    width: 16px;
    transform: scale(1);
    background: #ffffff;
    box-shadow: 0 0 0 0 rgba(0, 0, 0, 1);
    animation: pulse-dark 2s infinite;
    display: inline-block;
}


@keyframes pulse-dark {
	0% {
		transform: scale(.85);
		box-shadow: 0 0 0 0 rgba(0, 0, 0, .7)
	}
	70% {
		transform: scale(1)
	}
	99% {
		box-shadow: 0 0 0 8px rgba(0, 0, 0, 0)
	}
	100% {
		transform: scale(.85);
		box-shadow: 0 0 0 0 rgba(0, 0, 0, 0)
	}
}

.ts__card_socialAction a{text-decoration: none;}


.tb_ms_slider_next_arrow-next, .tb_ms_slider_pre_arrow-prev {
    border-radius: 3px;
    -webkit-border-radius: 3px;
    -moz-border-radius: 3px;
    background-color: #fff;
    opacity: .8;
    cursor: pointer;
    color: transparent;
    width: 35px;
    height: 50px;
    overflow: hidden;
    top: 50%;
    margin-top: -27px;
    position: absolute;
    display: flex;
    align-items: center;
    justify-content: center;
    border: 0;
}

.tb_ms_slider_pre_arrow-prev,
.tb_ms_slider_next_arrow-next {
    box-shadow: 0 0 3px rgba(0, 0, 0, 0.6);
    color: #000;
    font-size: 22px;
}

.tb_ms_slider_pre_arrow-prev.splide__arrow--prev{
    left: 8px;
    border-radius: 0px 3px 3px 0px;
    -webkit-border-radius: 0px 3px 3px 0px;
    -moz-border-radius: 0px 3px 3px 0px;
}

.tb_ms_slider_next_arrow-next.splide__arrow--next {
    right: 8px;
    border-radius: 3px 0px 0px 3px;
    -webkit-border-radius: 3px 0px 0px 3px;
    -moz-border-radius: 3px 0px 0px 3px;
}

.ts_ms_card_socialAction > a:hover > div {
    transform: rotatey(360deg);
    transition: all 0.6s ease-in-out;
}

.tb_ms_slider_pre_arrow-prev:hover, .tb_ms_slider_next_arrow-next:hover{
    opacity: 0.6;
} 

@media screen and (max-width:767px){
    .ts_ms_Post_hover:hover {
        display: none;
    }

}